@import url(https://fonts.googleapis.com/css?family=Exo:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i&display=swap);
body {
  font-family: "Exo";
  margin: 0;
}

.app {
  display: grid;
  grid-template-rows: 120px 1fr 100px;
  height: 100vh;
}

.nav-list {
  list-style: none;
  text-decoration: none;
  height: 110px;
  margin: 0;
  display: flex;
  align-items: center;
  position: fixed;
  width: 100%;
  background-color: black;
  z-index: 5;
}

.nav-list-header-links {
  display: flex;
  align-items: center;
  margin: 0 155px 0 auto;
}

.nav-list-link {
  text-decoration: none;
  font-size: 20px;
  outline: none;
  color: white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  padding: 10px;
  z-index: 1;
  cursor: pointer;
}

.nav-list-link-logo {
  text-decoration: none;
  outline: none;
  padding: 10px;
  z-index: 1;
  margin: 0 3vw;
}

.iframe-link {
  position: relative;
  z-index: 2;
}

.nav-list-link:hover {
  color: red;
}

.phone-catalog {
  margin-right: 30px;
}

.home-page-link {
  margin-right: 30px;
  background-color: black;
  margin-left: auto;
}

.cart {
  color: white;
  text-decoration: none;
  font-size: 20px;
  margin-right: 30px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.cart:hover {
  color: red;
}

.cart-amount {
  position: relative;
  text-align: center;
  width: 13px;
  font-size: 18px;
  border-radius: 10px;
}

.cart-amount-none {
  display: none;
}

.phone {
  width: 300px;
  height: 300px;
}

.home-page {
  background-color: black;
}

.home-page-img {
  position: relative;
  object-fit: contain;
  margin: -10px 0;
}

.home-page-sign {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 45px;
  font-weight: 500;
  line-height: 30px;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
}

.search_field-grid {
  margin: 45px 0;
  display: grid;
  justify-content: center;
}

.search_field {
  width: 600px;
  padding: 0 20px;
  font-size: 24px;
  height: 50px;
  border-radius: 30px;
  border: 0;
  box-shadow: 0 0 4px gray;
  font-family: "Exo";
  outline: none;
}

.catalog {
  display: grid;
  grid-template-columns: repeat(4, 130px);
  justify-content: center;
  justify-items: center;
  grid-column-gap: 170px;
  margin: 0 0 100px;
}

.catalog_phone {
  border-radius: 10px;
  padding: 10px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.catalog_phone:hover {
  box-shadow: 0 0 5px gray;
}

.catalog_phone-text-decoration {
  text-decoration: none;
  color: black;
}

.catalog_phone-add-grid {
  display: grid;
  justify-content: center;
  align-content: center;
  margin: 20px;
}

.catalog_phone-add {
  font-family: "Exo";
  display: block;
  background-color: white;
  text-decoration: none;
  color: black;
  border: 0;
  font-size: 18px;
  padding: 20px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}

.catalog_phone-add:hover {
  box-shadow: 0 0 3px gray;
  background-color: black;
  border-radius: 10px;
  color: white;
}

.catalog_phone-img {
  width: 200px;
  height: 250px;
}

.catalog_phone-name {
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  outline: none;
  color: black;
  padding: 10px;
  text-transform: uppercase;
}

.phone-details_header {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}

.phone-details_images {
  position: relative;
  margin: 0, auto;
  width: 60%;
  padding: 20px 5px 10px;
  display: flex;
  flex-wrap: nowrap;
  margin-top: 20px;
}

.phone-details_name {
  text-align: center;
  font-size: 22px;
  margin: 10px 30px 0;
  font-family: "Exo";
}

.phone-details-wrapper_description {
  display: flex;
  justify-content: center;
}

.phone-details_description {
  padding: 20px;
  font-size: 18px;
  font-weight: 300;
  width: 740px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.error-message {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}

.error-image {
  margin-top: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 400px;
  height: 400px;
  background-image: url(/react_phone-catalog/static/media/404.533b56bb.jpg);
}

.back-to-home-error-button {
  text-decoration: none;
  color: black;
  border: 1px solid black;
  padding: 10px;
  font-size: 30px;
  border-radius: 10px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.back-to-home-error-button:hover {
  background-color: black;
  color: white;
}

.section-specifications {
  background-color: black;
}

.specifications {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  color: white;
  background-color: black;
  font-weight: 100;
  margin: 20px 93px 1px;
}

.specifications_sign {
  color: white;
  background-color: black;
  margin-top: 30px;
  text-align: center;
  font-size: 30px;
  padding: 40px;
}

.specifications_details {
  font-size: 16px;
  border: 1px solid black;
  padding: 20px;
}

.specifications_details-sign {
  font-weight: bold;
}

.phone-details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
  margin: 50px 110px 0;
}

.phone-details_add {
  display: block;
  background-color: white;
  font-family: "Exo";
  text-decoration: none;
  color: black;
  padding: 20px;
  box-shadow: 0 0 2px black;
  border-radius: 10px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  margin-right: auto;
}

.phone-details_add:hover {
  background-color: black;
  color: white;
}

.cart-phones_link-back {
  text-decoration: none;
  display: inline-block;
  color: black;
  text-align: center;
  width: 140px;
  box-shadow: 0 0 2px black;
  padding: 10px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 10px;
  margin: 0 73px 0 110px;
}

.cart-phones_link-back-phone-details {
  text-decoration: none;
  color: black;
  text-align: center;
  width: 140px;
  box-shadow: 0 0 2px black;
  padding: 10px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 10px;
  margin-left: auto;
}

.cart-phones_link-back-phone-details:hover {
  background-color: black;
  color: white;
}

.cart-phones_link-back:hover {
  background-color: black;
  color: white;
}

.cart-phones {
  display: grid;
  grid-template-columns: 1fr 3fr;
  align-items: center;
  margin: 30px 0 50px;
}

.cart-phone_sign {
  font-weight: 300;
  margin: 55px 0 0 205px;
  text-align: center;
  font-size: 18px;
  margin-right: auto;
}

.cart-phone_table-grid {
  display: grid;
  justify-items: center;
}

.cart-phone_table {
  grid-template-columns: 1fr 1fr;
  display: grid;
  justify-items: center;
  align-items: center;
  margin-bottom: 50px;
}

.cart-phone_table-img {
  width: 300px;
  height: 300px;
  text-align: center;
}

.cart-phone_table-name {
  text-align: center;
}

.cart-phone_table-amount-btn {
  background-color: white;
  border: 1px solid black;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
  width: 30px;
  height: 30px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 10px;
  font-size: 25px;
  font-family: "Exo";
  display: block;
}

.cart-phone_table-amount-btn:hover {
  background-color: black;
  color: white;
}

.cart-phone_table-amount-sign {
  font-size: 20px;
}

.cart-phone_table-remove-btn {
  margin: 30px 0;
  background-color: white;
  border: 1px solid black;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
  height: 30px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  display: block;
  font-size: 15px;
  font-family: "Exo";
  border-radius: 10px;
  width: 200px;
}

.cart-phone_table-remove-btn:hover {
  background-color: black;
  color: white;
}

.footer-size {
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer {
  background-color: black;
  margin-left: 6%;
}

.footer-list-links {
  display: flex;
  align-items: center;
  margin: 0 24% 0 auto;
}

.footer-link {
  text-decoration: none;
  color: white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  width: 110px;
  display: block;
  margin-top: -10px;
}

.footer-link:hover {
  color: red;
}

.contacts-details {
  display: grid;
  grid-template-columns: 1fr;
  margin: 0 20px 20px 110px;
  font-weight: 400;
}

.contacts-link {
  color: black;
}

@media (max-width: 1140px) {

  body {
    font-size: 18px;
  }

  .home-page-sign {
    font-size: 35px;
  }

  .catalog {
    grid-template-columns: repeat(3, 100px);
  }

  .phone-details {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .cart-phones_link-back-phone-details {
    width: 170px;
    margin: -40px 0 0 340px;
  }

  .phone-details_add {
    width: 150px;
    margin-top: -30px;
  }
}

@media (max-width: 784px) {

  .nav-list {
    grid-template-columns: 1fr;
    grid-gap: 20px;
  }

  .home-page-sign {
    opacity: 0;
  }

  .catalog {
    grid-template-columns: repeat(3, 100px);
  }

  .phone-details {
    grid-template-columns: 1fr 1fr;
  }

  .search_field {
    margin-top: 20px;
    width: 490px;
  }

  .phone-details_name {
    font-size: 20px;
    margin-left: 50px;
  }

  .specifications {
    grid-template-columns: 1fr 1fr;
  }

  .cart-phones_link-back {
    padding: 5px;
    text-align: center;
  }

  .cart-phone_table-remove-btn {
    margin: 30px 0;
    width: 150px;
  }

  .animation {
    margin-top: 140px;
  }

  .cart-phone_table {
    grid-template-columns: 1fr;
  }

  .cart-phone_table-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
  }
}

@media (max-width: 783px) {
  .catalog {
    grid-template-columns: 1fr 1fr;
  }

  .phone-details {
    grid-template-columns: 1fr;
  }

  .specifications {
    grid-template-columns: 1fr;
  }

  .phone-details-wrapper_description {
    display: flex;
    justify-content: center;
    width: 70%;
  }

  .cart-phone_sign {
    font-weight: 300;
    margin: 15px 0 0;
    text-align: center;
    font-size: 18px;
    width: 200px;
  }

  .cart-phones_link-back {
    margin: 10px 73px 0 70px;
  }

  .cart-phone_table-remove-btn {
    margin-left: 100%;
  }

  .cart-phones_link-back-phone-details {
    width: 170px;
    margin: -40px 0 0 340px;
  }

  .nav-list-header-links {
    font-size: 15px;
    margin: 0 50px;
  }

  .nav-list-link-logo {
    margin: 0;
  }

  .animation {
    margin-top: 20px;
  }
}

/* load animation */

.animation {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.cssload-square {
  width: 8px;
  height: 29px;
  background: rgb(0,0,0);
  border-radius: 10px;
  display: block;
  animation: cssload-turn 2s ease infinite;
  -o-animation: cssload-turn 2s ease infinite;
  -ms-animation: cssload-turn 2s ease infinite;
  -webkit-animation: cssload-turn 2s ease infinite;
  -moz-animation: cssload-turn 2s ease infinite;
}

.cssload-top {
  position: absolute;
  left: 40%;
  top: 50%;
  transform: rotate(90deg);
  -o-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
}

.cssload-bottom {
  position: absolute;
  left: 40%;
  top: 50%;
  transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
}

.cssload-left {
  position: absolute;
  left: 40%;
  top: 50%;
}

.cssload-right {
  position: absolute;
  left: 40%;
  top: 50%;
  transform: rotate(-180deg);
  -o-transform: rotate(-180deg);
  -ms-transform: rotate(-180deg);
  -webkit-transform: rotate(-180deg);
  -moz-transform: rotate(-180deg);
}

@keyframes cssload-turn {
  0% {
    -webkit-transform: translateX(0) translateY(0) rotate(0);
            transform: translateX(0) translateY(0) rotate(0);
  }
  50% {
    -webkit-transform: translateX(400%) translateY(100%) rotate(90deg);
            transform: translateX(400%) translateY(100%) rotate(90deg);
  }
  100% {
    -webkit-transform: translateX(0) translateY(0) rotate(0);
            transform: translateX(0) translateY(0) rotate(0);
  }
}

@-webkit-keyframes cssload-turn {
  0% {
    -webkit-transform: translateX(0) translateY(0) rotate(0);
  }
  50% {
    -webkit-transform: translateX(400%) translateY(100%) rotate(90deg);
  }
  100% {
    -webkit-transform: translateX(0) translateY(0) rotate(0);
  }
}

.form {
  display: block;
}

.wrapper {
  display: flex;
  justify-content: space-between;
}

.inner {
  display: flex;
  align-items: center;
  height: 40px;
  box-sizing: border-box;
  padding: 10px;
}

.input {
  border: none;
  outline: none;
  cursor: pointer;
  background: transparent;
  font-size: 16px;
  width: 50px;
  -webkit-transition: width 0.3s ease;
  transition: width 0.3s ease;
}

.search_img {
  margin-right: 5px;
  padding: 0;
  display: flex;
  align-items: center;
}

.form:focus-within {
  background: rgba(0,0,0,0.05);
  border-bottom: 2px solid gray;
}

button {
  padding: 3px;
  line-height: 0;
  background: transparent;
  font-size: 16px;
  color: #757575;
  cursor: pointer;
  border: none;
  outline: none;
  display: none;
}

button svg path {
  fill: #757575;
}

.clear:focus,
.clear:hover,
.close:focus svg path,
.close:hover svg path {
  fill: #424242;
  color: #424242;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.input:focus {
  width: 200px;
  cursor: auto;
  cursor: initial;
}

.bulkhead {
  width: 1px;
  height: 100%;
  background: #757575;
  margin: 0 5px;
  display: none;
}

.form:focus-within .clear,
.form:focus-within .close,
.form:focus-within .bulkhead {
  display: block;
}

